.app-container {
    display: flex;
    justify-content: center;
    background-color: #161625;
    min-height: 100vh;
  }
  .cowin-dashboard-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    width: 80%;
    max-width: 1110px;
  }
  
  .logo-container {
    display: flex;
    align-items: center;
    margin-top: 20px;
  }
  .logo {
    width: 50px;
    height: 50px;
  }
  
  .logo-heading {
    color: #2cc6c6;
    font-family: 'roboto';
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 1.5;
    margin-left: 10px;
  }
  .heading {
    color: #cbd5e1;
    font-family: 'roboto';
    font-style: normal;
    font-weight: 500;
    font-size: 30px;
    line-height: 1.5;
  }
  .loading-view {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-grow: 1;
  }
  
  .failure-view {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .failure-image {
    width: 50%;
    margin: 10px;
  }
  .failure-text {
    color: white;
    font-family: 'bree serif';
    line-height: 1.4;
    font-size: 25px;
    text-align: center;
  }
  